.section {
  position: relative;
  display: flex;
  flex-direction: column;

  padding: var(--padding-and-margin) 0;

  scroll-behavior: smooth;

  @include for-size(tablet-landscape-up) {
    padding: calc(var(--padding-and-margin) * 2) 0;
  }

  &--bg-1 {
    position: relative;
    min-height: 37.5rem;
    background-color: $primary-color;
    background-image: url("../img/bg/bg-1-425.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50% 50%;

    @include for-size(tablet-portrait-up) {
      background-image: url("../img/bg/bg-1-900.png");
    }
    @include for-size(tablet-landscape-up) {
      background-image: url("../img/bg/bg-1-1200.png");
    }
    @include for-size(desktop-up) {
      background-image: url("../img/bg/bg-1.png");
    }

    &::before {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 20rem;
      background-image: url("../img/bg/bg-11-425.png");
      background-repeat: no-repeat;
      background-size: 100% 100%;
      background-position: 50% 50%;

      @include for-size(tablet-portrait-up) {
        background-image: url("../img/bg/bg-11-900.png");
      }
      @include for-size(tablet-landscape-up) {
        background-image: url("../img/bg/bg-11-1200.png");
      }
      @include for-size(desktop-up) {
        background-image: url("../img/bg/bg-11.png");
      }
    }
  }
  &--bg-3 {
    background-color: #fefaee;

    background-image: url("../img/bg/bg-4-425.jpg");
    background-repeat: repeat;
    background-size: contain;
    background-position: 50% 50%;
    overflow: hidden;

    @include for-size(tablet-portrait-up) {
      background-image: url("../img/bg/bg-4-900.jpg");
    }
    @include for-size(tablet-landscape-up) {
      background-image: url("../img/bg/bg-4-1200.jpg");
      background-repeat: no-repeat;
      background-size: cover;
      background-position: -2px 136%;
    }
    @include for-size(desktop-up) {
      background-image: url("../img/bg/bg-4.jpg");
    }


  }
  &--bg-4 {
    background-color: #f5f5f5;

  }
  &--bg-6 {
    background-color: #ffffff;
  }

  &__row {
    @include grid-row();
  }

  &__col {
    @include grid-col(
      (
        xs: 12,
        sm: 6
      )
    );
  }



  &__title {
    margin-bottom: $padding-and-margin;

    font-family: $mainFont-bold;
    font-weight: $mainFontWeight-bold;
    @include ff("font-size", $mainTitleSection);
    line-height: normal;

    &--h1 {
      @include ff("font-size", $mainTitleSectionh1);
    }
  }
  &__title-block {
    margin-bottom: $padding-and-margin;

    font-family: $mainFont-bold;
    font-weight: $mainFontWeight-bold;
    @include ff("font-size", $mainTitleh1);
    line-height: normal;
  }

  & .container {
    position: relative;
  }
  &__link {
    position: absolute;
    top: 0;
    right: 2.5rem;
    display: flex;
    align-items: center;

    color: $primary-color;
    line-height: normal;
    text-decoration: none;

    @include for-size(phone-only) {
      right: 1rem;
      & .section__icon {
        display: none;
      }
    }

    &:focus,
    &:hover {
      & .section__icon {
        transform: translateX(10%);
        transition: all 0.5s;
      }
    }
  }
  &__icon {
    margin-left: 0.625rem;
    transform: translateX(-10%);
    transition: all 0.5s;
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    @include listReset();
  }
  &__item {
    flex-grow: 1;

    margin-bottom: var(--mb);
    max-width: calc(100% / var(--count-column) - var(--padding-and-margin));

    @include for-size(phone-only) {
      --count-column: 1;
      --padding-and-margin: 0;
    }
    @include for-size(tablet-portrait-up) {
      margin-right: var(--padding-and-margin);
      --count-column: 2;
    }

    @include for-size(tablet-landscape-up) {
      --count-column: 3;
    }
    @include for-size(desktop-up) {
      --count-column: 4;
    }
  }
  & #map {
    width: 40%;

    @include for-size(tablet-landscape-down) {
      height: 350px;
      width: 100%;
      margin-bottom: 0;
      overflow: hidden;
    }
  }
}

.section__btn {
  z-index: 3;
  position: absolute;
  bottom: -20px;
  left: 50%;
  transform: translateX(-50%);
  background-color: transparent;
  border: none;
  cursor: pointer;
  scroll-behavior: smooth;
}

.section__btn svg {
  width: 30px;
  height: 30px;

}

.section .swiper-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
