.number {
  margin-bottom: var(--padding-and-margin);
  font-family: $mainFont-bold;
  font-weight: $mainFontWeight-bold;
  &__list {
    @include listReset();
    margin-bottom: var(--padding-and-margin);
  }
  &__item {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    padding: var(--padding-and-margin) 0;
    border-bottom: 1px solid $border-base;
  }
  &__icon {
    width: 3rem;
    height: 3rem;
  }
  &__info {
    flex: 0 0 40%;
    @include ff('font-size', $mainTitleh2);
  }
  &__count {
    flex: 0 0 30%;
    @include ff(
      "font-size",
      (
        375px: 40px,
        1920px: 125px
      )
    );
    line-height: normal;
  }
  &__footer {
    text-align: center;
    &-price {
      @include ff(
        "font-size",
        (
          375px: 40px,
          1920px: 110px
        )
      );
      line-height: normal;
      color: $danger-color;
    }
  }
}
