.accordion {
  @include listReset();
  padding: $padding-and-margin 0;
  margin-bottom: $padding-and-margin;

  &--bg {
    background-color: $default-color;
    padding: $padding-and-margin;
  }
  &--bg-2 {
    background-color: #f5feff;
    padding: $padding-and-margin;
  }
  &__item-title {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    width: 100%;
    text-align: left;
    border: none;
    background-color: transparent;

    &--border {
      margin-bottom: calc(var(--padding-and-margin) / 2);
      border-bottom: 1px solid $border-base;
    }

    & svg {
      fill: $primary-color;
      // fill-rule:evenodd;
      stroke: $default-color;
      stroke-width: 0.4;
      stroke-linejoin: round;

      transform: rotate(180deg);
    }

    &.collapsed {
      & svg {
        transform: rotate(0deg);
        -webkit-transform: rotate(0deg);
        -moz-transform: rotate(0deg);
        -ms-transform: rotate(0deg);
        -o-transform: rotate(0deg);
      }
    }
  }
  &__desc {
    position: relative;
    max-height: 0;
    opacity: 0;
    overflow: hidden;
    transition: all 0.5s;

    &.active {
      max-height: auto;
      opacity: 1;
      transition: all 0.5s;
    }
  }
  &__btn-icon {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__text {
    padding: $padding-and-margin 0;

    &--border {
      margin-bottom: calc(var(--padding-and-margin) / 2);
      border-bottom: 1px solid $border-base;
    }
  }
  &__title {
    position: relative;
    margin-right: auto;
    // margin-bottom: $padding-and-margin;
    max-width: 90%;
    @include ff("font-size", $mainTitleh2);
    font-family: $mainFont-bold;
    font-weight: $mainFontWeight-bold;
    color: $acc-color;
    transition: 0.3s;

    &:hover {
      cursor: pointer;
    }
  }

  &__title-country {
    display: block;
    margin-top: 10px;
    font-size: 80%;
    color: $light-color;
  }
  &__pic {
    width: 3rem;
    height: 3rem;
    margin-right: 0.625rem;
  }

  & .expand-collapse {
    position: absolute;
    top: 0;
    right: -10%;
  }
  &__description {
    position: relative;
    max-height: 0;
    // padding-top: 20px;
    line-height: normal;
    color: $text-color;
    overflow: hidden;

    transition: max-height 0.2s ease-out;
    p {
      margin-bottom: $padding-and-margin;
    }
    &-icon {
      position: absolute;
      top: 0;
      right: 5px;
    }
  }
  &__list {
    @include listReset();

    @include for-size(tablet-portrait-up) {
      padding-left: 3.625rem;
    }
  }
  &__list-item {
    margin-bottom: calc(var(--padding-and-margin) / 2);
  }
  &__list-link {
    display: flex;
    justify-content: space-between;

    font-family: $mainFont-bold;
    font-weight: $mainFontWeight-bold;
  }
  &__list-price {
    color: $text-color;
  }
}

