.accordion--yellow-arrow {
  & .accordion__btn-open {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;

    & svg {
      width: 30px;
      height: 30px;
      fill: $warning-color;
      fill-rule: evenodd;
      stroke: $text-color;
      stroke-width: 0.4;
      stroke-linejoin: round;
    }
  }
  & .accordion__btn-close {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    transform: rotate(180deg);
    overflow: hidden;

    & svg {
      fill: $warning-color;
      fill-rule: evenodd;
      stroke: $text-color;
      stroke-width: 0.4;
      stroke-linejoin: round;
    }
  }
}
