.card {
  display: block;
  margin-bottom: $padding-and-margin;
  // max-width: 18.75rem;
  color: $text-color;

  &:focus,
  &:hover {
    color: $text-color;
    & .card__inner {
      transition: .3s;
      box-shadow: 0 0 25px $border-base;
    }
  }
  &__inner {
    border: 1px solid $border-base;
    border-radius: 10px;
    overflow: hidden;
  }
  &__header {
    position: relative;
  }
  &__info {
    padding: var(--padding-and-margin) calc(var(--padding-and-margin) / 2);
  }
  &__info-list {
    @include listReset();
    margin-bottom: $padding-and-margin;
  }
  &__info-item {
    display: flex;
    align-items: center;
    margin-bottom: calc(var(--padding-and-margin) / 2);
  }
  &__info-icon {
    width: 1.25rem;
    margin-right: 0.625rem;
  }
  &__info-val {
    font-family: $mainFont-bold;
    font-weight: $mainFontWeight-bold;

    &--price {
      color: $danger-color;
    }
  }
  &__pic {
    height: 12.5rem;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-color: $primary-color;
  }
  &__icon-hot {
    position: absolute;
    bottom: -20px;
    right: 0.625rem;
    width: 2.5rem;
    height: 2.5rem;
  }
  &__title {
    display: inline-block;
    margin-bottom: $padding-and-margin;
    font-family: $mainFont-bold;
    font-weight: $mainFontWeight-bold;
    @include ff("font-size", ($mainTitle));
    line-height: normal;
    color: $primary-color;
  }
  &__val {
    margin-bottom: $padding-and-margin;
    font-family: $mainFont-bold;
    font-weight: $mainFontWeight-bold;
    &--price {
      @include ff("font-size", $mainTitle);

      color: $danger-color;
    }
  }
  &__button {
    width: 100%;
    display: block;
    text-align: center;
    @include ff("font-size", $cardButton);
  }
}
