/* Модальное окно с выбором туристов */

.modal-dialog {
  margin-top: 10%;
}

.tc_content > span {
  font-family: Open Sans, Trebuchet MS, arial, sans-serif;
  font-weight: 700;
  line-height: 25px;
  font-size: 18px;
  text-align: center;
  text-anchor: middle;
}
.tc_TouristsPanel {
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
  box-shadow: 0 0 0 1px #ccc;
}
.tc_TouristElement {
  width: 100%;
  height: 40px;
  display: flex;
  align-items: center;
  border: 1px solid #ddd;
  border-radius: 2px;
  margin-top: 10px;
}
.tc_TouristAction {
  width: 50px;
  height: 100%;
  display: flex;
  justify-content: center;
  background-color: #efefef;
  cursor: pointer;
}
.tc_TouristCount {
  width: 90%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: Open Sans, Trebuchet MS, arial, sans-serif;
  font-weight: 700;
  line-height: 19px;
  font-size: 14px;
  text-align: center;
  text-anchor: middle;
}
.tc_SubmitButton {
  background-color: #fff;
  font-size: 13px;
  line-height: 40px;
  box-sizing: border-box;
  border-radius: 20px;
  width: 90%;
  height: 40px;
  color: #777;
  padding: 0;
  margin: 20px auto;
  border: 1px solid #ddd;
  background-image: none;
  box-shadow: none;
  text-align: center;
  text-shadow: none;
  float: none;
  cursor: pointer;
}
.tc_SubmitButton.tc_ButtonActive {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  background-color: #185584;
  border-color: #185584;
  margin: 10px auto;
  font-family: Open Sans, Trebuchet MS, arial, sans-serif;
  font-weight: 700;
  font-size: 16px;
  text-align: center;
}
.tc_TouristButton {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  font-family: Open Sans, Trebuchet MS, arial, sans-serif;
  font-weight: 700;
  line-height: 19px;
  font-size: 14px;
  text-align: center;
  text-anchor: middle;
}
.tc_TouristPickElement {
  width: 100%;
  height: auto;
  display: flex;
  align-items: center;
  border: 1px solid #ddd;
  border-radius: 2px;
  margin-top: 10px;
  padding: 5px;
}
.tc_pickHeader {
  width: 100%;
  height: auto;
  padding: 10px;
}
.tc_pickAge {
  margin: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 90px;
  height: 40px;
  box-shadow: 0 0 0 1px #ccc;
}
.tc_symbol {
  width: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  font-weight: 900;
}
.modal-body-turistic {
  padding: 15px 0;
}
.modal-backdrop {
  display: none;
}
.search-form__val {
  background-color: #fff;
}
.search-form__turist button {
  border: none;
}

/* Конец - Модальное окно с выбором туристов*/
