.page-footer {
  padding: 2.5rem 0;
  font-family: $mainFont-bold;
  font-weight: $mainFontWeight-bold;
  background-color: $secondary-color;
  color: $default-color;

  &__container {
    display: flex;
    flex-wrap: wrap;
  }

  &__inner {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;

    @include for-size(tablet-portrait-up) {
      justify-content: space-between;
    }
  }

  &__list {
    @include listReset();
    display: flex;
    flex-wrap: wrap;
    margin: 1.25rem 0;
  }
  &__list-link {
    padding: 5px 10px;
    line-height: 1.6;
    color: $default-color;
    text-decoration: none;

    &:focus,
    &:hover {
      color: $link-footer-color-hover;
    }
  }
  &__list-item {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
  }

  &__platform {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
