*,
*::before,
*::after {
  box-sizing: border-box;
  outline: none;
}

* {
  margin: 0;
  padding: 0;
}

html,
body {
  height: 100%;
}

body {
  position: relative;
  margin: 0;
  padding: 0;
  font-family: $mainFont;
  // font-size: $mainFontSize;
  font-weight: $mainFontWeight;
  line-height: $mainLineHeight;
  color: $text-color;

  scroll-behavior: smooth;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-bottom: 20px;
  font-family: $mainFont;
  font-size: 1.15rem;
  font-weight: 600;
  line-height: normal;
}

a:not([class*='']) {
  text-decoration: none;
}
// footer к низу

.wrapper {
  width: 100%;
  min-height: 100%;
}
.main {
  min-height: calc(100% - 400px);
  overflow-x: hidden;
}

.box-center {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  // height: 100%;
  // width: 100%;
}

.overlay {
  overflow: hidden;
  height: 100vh;
}

.scroll-content {
  overflow-y: auto;
  height: 100%;
}

.active {
  max-height: auto;
  transition: 0.5s;
  overflow: visible;
}
.closed {
  max-height: 0;
  transition: 0.5s;
  overflow: hidden;
}

.no-list {
  @include listReset();
}

.small {
  font-size: 65%;
  @media (min-width: 767px) {
    font-size: 80%;
  }
}

.dropdown-menu {
  padding: 1rem 0;
}

.dropdown-menu.show {
  transform: translate3d(0, 30px, 0px) !important;
}

.dropdown-item.link {
  display: block;
  color: $link-color;
}

.dropdown-item:focus,
.dropdown-item:hover {
  background-color: $primary-color;
}

.map {
  position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.container--center {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
  }


  .contacts {
    &__info {
      min-height: 20rem;

      @include for-size(tablet-portrait-up) {
        min-height: 40rem;
      }
    }
  }

  .link {
    color: $link-color;
  }
