.purchase-stages {
  padding: var(--padding-and-margin) 0;
  &__list {
    display: flex;
    flex-wrap: wrap;
    max-width: 77.8125rem;
    @include listReset();
  }
  &__item {
    position: relative;
    flex: 0 0 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: calc(var(--padding-and-margin) / 2);
    margin-bottom: $padding-and-margin;
    text-align: center;

    @include for-size(tablet-landscape-up) {
      flex: 0 0 30%;
    }
    @include for-size(desktop-up) {
      flex: 0 0 20%;
    }

    &::before {
      content: "";
      position: absolute;
      z-index: 4;
      top: 60px;
      right: 70%;
      width: 200px;
      height: 2px;
      border: 2px dashed $text-color;
    }
    &::after {
      content: "";
      position: absolute;
      top: 55px;
      right: 68.5%;
      width: 15px;
      height: 15px;
      background-repeat: no-repeat;
      background-image: url('../img/icons/icon-path_arrow.svg');
    }
  }
  &__icon {
    z-index: 5;
    position: relative;
    width: 6.25rem;
    height: 6.25rem;
    margin-bottom: var(--padding-and-margin);


  }
  &__title {
    margin-bottom: $padding-and-margin;
    font-family: $mainFont-bold;
    font-weight: $mainFontWeight-bold;
    @include ff("font-size", $mainTitle);
  }
  &__info {
    font-family: $mainFont-bold;
    font-weight: $mainFontWeight-bold;
    line-height: normal;
  }
  &__path-plane {
    position: absolute;
    top: 0;
    right: -80px;

    &::before {
      content: "";
      position: absolute;
      z-index: 4;
      top: 60px;
      right: 70%;
      width: 200px;
      height: 2px;
      border: 2px dashed $text-color;
    }
    &::after {
      content: "";
      position: absolute;
      top: 38px;
      right: 68.5%;
      width: 50px;
      height: 50px;
      background-repeat: no-repeat;
      background-image: url('../img/icons/icon-path_plane.svg');
    }
  }
}
