.search-form {
  position: relative;
  z-index: 2;
  width: 100%;
  // max-width: 1000px;

  &__inner {
    // padding: 0 15px;
    @include grid-container();
  }
  &__title {
    font-family: $mainFont-bold;
    font-weight: $mainFontWeight-bold;
    padding-bottom: calc(var(--padding-and-margin) / 2);
  }
  &__list {
    position: relative;
    z-index: 2;

    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    @include listReset();
    @include grid-row();

    background-color: $default-color;
    border-radius: 10px;
    box-shadow: 0 0 25px darken($border-base, 9%);
  }
  &__item {
    position: relative;

    display: flex;
    flex-direction: column;

    &:last-child {
      padding: 0;
      border-right: none;
      @include for-size(tablet-portrait-up) {
        margin-right: -1px;
      }
    }
    &:nth-child(5) {
      border-right: none;
    }

    @include grid-col(
      (
        xs: 12,
        sm: 4,
        md: 4,
        lg: 2
      )
    );

    padding: 0;

    @include for-size(phone-only) {
      border-right: none;

      &:first-child {
        & .search-form__item-inner {
          border-top-right-radius: 10px;
          border-top-left-radius: 10px;
        }
      }
    }
    @include for-size(tablet-portrait-up) {
    }
    @include for-size(tablet-portrait-up) {
      &:first-child {
        & .search-form__item-inner {
          border-top-left-radius: 10px;
        }
      }
      &:nth-child(3) {
        & .search-form__item-inner {
          border-top-right-radius: 10px;
        }
      }
      &:nth-child(4) {
        & .search-form__item-inner {
          border-bottom-left-radius: 10px;
        }
      }
    }
    @include for-size(desktop-up) {
      &:first-child {
        & .search-form__item-inner {
          border-bottom-left-radius: 10px;
          border-top-left-radius: 10px;
        }
      }
      &:nth-child(3) {
        & .search-form__item-inner {
          border-top-right-radius: 0;
        }
      }
      &:nth-child(4) {
        & .search-form__item-inner {
          border-bottom-left-radius: 0;
        }
      }
    }
    @include for-size(big-desktop-up) {
    }
  }
  &__item-inner {
    position: relative;
    z-index: 8;
    display: flex;
    flex-direction: column;
    height: 100%;
    background-color: $default-color;
    border-right: 1px solid $border-base;

    box-shadow: 0 25px 25px -25px darken($border-base, 9%);

    @include grid-col(
      (
        xs: 12
      )
    );

    @include for-size(tablet-portrait-down) {
      height: auto;
    }
  }
  &__field {
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    padding: calc(var(--padding-and-margin) / 2) 0;

    background-color: $default-color;
  }
  &__val {
    display: block;
    margin-top: auto;
    margin-bottom: 0;
    padding-right: 30px;
    width: 100%;

    font-family: $mainFont-bold;
    font-weight: $mainFontWeight-bold;
    font-size: small;

    border: none;
    background-repeat: no-repeat;
    background-position: 100% 50%;
    // background-size: contain;

    &--icon-one {
      background-image: url("../img/icons/icon-search_where.svg");
    }
    &--icon--two {
      background-image: url("../img/icons/icon-search_adlt.svg");
    }

    &--icon-adlt {
      background-image: url("../img/icons/icon-search_adlt.svg");
      background-position: 95% 0;
    }
    &--icon--kids {
      background-image: url("../img/icons/icon-search_kids.svg");
      background-position: 95% 0;
    }
  }

  &__btn-icon {
    position: absolute;
    bottom: 19px;
    right: 7px;

    width: 1rem;
    height: 1rem;
    fill: $primary-color;
    background-color: #fff;
  }

  &__group {
    display: flex;
    flex-direction: column;
    padding-bottom: calc(var(--padding-and-margin) / 2);
    border: none;
    &-title {
      padding-top: calc(var(--padding-and-margin) / 2);
      font-family: $mainFont-bold;
      font-weight: $mainFontWeight-bold;
      font-size: inherit;
    }
    &-list {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      @include listReset();
      @include grid-row();
    }
    &-item {
      @include grid-col(
        (
          xs: 6
        )
      );
    }
    &-field {
      position: relative;
      padding: calc(var(--padding-and-margin) / 2) 0;
    }
  }
  &__btn-submit {
    width: 100%;
    height: 100%;
    border: none;
  }
  &__inner-options {
    position: absolute;
    z-index: 0;
    top: 74px;
    left: 0;
  }
  &__options {
    position: absolute;
    z-index: 7;
    top: 85%;
    left: 0;

    width: 300px;

    background-color: $default-color;
    border-radius: 5px;

    @include for-size(desktop-down) {
      z-index: 9;
    }
    @include for-size(tablet-portrait-down) {
      position: relative;
      z-index: 5;
      top: 0;
      left: 0;
      width: auto;
    }

    @include for-size(phone-only) {
      left: -10px;
    }

    &.closed {
      height: 0;
      overflow: hidden;
      pointer-events: none;

      animation-name: close-opacity;
      animation-duration: 1s;
    }
    &.active {
      height: 150px;
      overflow: visible;
      overflow-y: auto;
      pointer-events: all;

      animation-name: open-opacity;
      animation-duration: 1s;

      @include for-size(tablet-portrait-down) {
        position: relative;
        top: 0;
        left: 0;
        height: auto;
        max-height: 200px;
      }
    }
    &-list {
      @include listReset();
      padding: var(--padding-and-margin) 0;
    }
    &-item {
      display: flex;
      justify-content: space-between;
      padding: calc(var(--padding-and-margin) / 2);
      color: $light-color;


      &:focus,
      &:hover {
        background-color: rgba(154, 154, 154, 0.25);
      }
      cursor: pointer;
    }
  }
}

.search-form {
  & .btn {
    font-family: $mainFont-bold;
    font-size: 0.85rem;
    font-weight: $mainFontWeight-bold;
    border: none;
  }
  & .btn:not([type="submit"]),
  & .custom-select {
    font-family: $mainFont-bold;
    font-size: 0.85rem;
    font-weight: $mainFontWeight-bold;
    border: none;
    text-align: left;
  }
}
